/**
 * Ghana Phone Number Validation Utility
 * Supports formats: 0XX XXX XXXX and 233XX XXX XXXX
 */

// Valid Ghana phone prefixes
const GHANA_PREFIXES = [
    '024', '054', '055', '027', '057', // MTN
    '026', '056', // AirtelTigo
    '020', '050', // Vodafone
    '059', // Glo
];

export interface PhoneValidationResult {
    isValid: boolean;
    formatted: string;
    error?: string;
}

/**
 * Validate Ghana phone number
 */
export function validateGhanaPhone(phone: string): PhoneValidationResult {
    if (!phone) {
        return {
            isValid: false,
            formatted: '',
            error: 'Phone number is required'
        };
    }

    // Remove all non-digit characters
    const cleaned = phone.replace(/\D/g, '');

    // Check if it starts with 233 (country code)
    if (cleaned.startsWith('233')) {
        const localNumber = cleaned.substring(3);

        if (localNumber.length !== 9) {
            return {
                isValid: false,
                formatted: phone,
                error: 'Phone number must be 9 digits after country code'
            };
        }

        const prefix = localNumber.substring(0, 3);
        if (!GHANA_PREFIXES.includes(prefix)) {
            return {
                isValid: false,
                formatted: phone,
                error: `Invalid network prefix. Must start with ${GHANA_PREFIXES.join(', ')}`
            };
        }

        return {
            isValid: true,
            formatted: `0${localNumber}` // Return in local format
        };
    }

    // Check local format (0XX XXX XXXX)
    if (cleaned.startsWith('0')) {
        if (cleaned.length !== 10) {
            return {
                isValid: false,
                formatted: phone,
                error: 'Phone number must be 10 digits'
            };
        }

        const prefix = cleaned.substring(0, 3);
        if (!GHANA_PREFIXES.includes(prefix)) {
            return {
                isValid: false,
                formatted: phone,
                error: `Invalid network prefix. Must start with ${GHANA_PREFIXES.join(', ')}`
            };
        }

        return {
            isValid: true,
            formatted: cleaned
        };
    }

    // If doesn't start with 0 or 233
    return {
        isValid: false,
        formatted: phone,
        error: 'Phone number must start with 0 or 233'
    };
}

/**
 * Format phone number for display (0XX XXX XXXX)
 */
export function formatPhoneDisplay(phone: string): string {
    const cleaned = phone.replace(/\D/g, '');

    if (cleaned.startsWith('233')) {
        const local = cleaned.substring(3);
        return `0${local.substring(0, 2)} ${local.substring(2, 5)} ${local.substring(5)}`;
    }

    if (cleaned.startsWith('0') && cleaned.length === 10) {
        return `${cleaned.substring(0, 3)} ${cleaned.substring(3, 6)} ${cleaned.substring(6)}`;
    }

    return phone;
}

/**
 * Convert to international format (233XXXXXXXXX)
 */
export function toInternationalFormat(phone: string): string {
    const cleaned = phone.replace(/\D/g, '');

    if (cleaned.startsWith('233')) {
        return cleaned;
    }

    if (cleaned.startsWith('0')) {
        return `233${cleaned.substring(1)}`;
    }

    return phone;
}

/**
 * Get network name from phone number
 */
export function getNetworkFromPhone(phone: string): string {
    const cleaned = phone.replace(/\D/g, '');
    let prefix = '';

    if (cleaned.startsWith('233')) {
        prefix = cleaned.substring(3, 6);
    } else if (cleaned.startsWith('0')) {
        prefix = cleaned.substring(0, 3);
    }

    if (['024', '054', '055', '027', '057'].includes(prefix)) return 'MTN';
    if (['026', '056'].includes(prefix)) return 'AirtelTigo';
    if (['020', '050'].includes(prefix)) return 'Vodafone';
    if (prefix === '059') return 'Glo';

    return 'Unknown';
}
